<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-09-03 19:35:18
 * @LastEditTime: 2019-09-27 11:59:59
 * @LastEditors: Please set LastEditors
 -->
<template>
  <!-- Myself -->
  <div class="contain">
     <div class="headTitle">
      <i @click="toPrePage" class="cubeic-arrow"></i>
      <p>{{$t('policyholder')}}</p>
    </div>
    <div class="hint">{{$t('policyholder_number')}}</div>
    <div class="form-wrap">
      <div class="title">{{$t('policyholder')}}</div>
      <div ref="inputInfo">
        <!-- 用户信息输入框 -->
        <div class="form-item">
          <p>{{$t('sex_title_1')}}</p>
          <!-- @blur="genderBlur" -->
          <cube-button class="cubeButton">
            <div
              @click="showPicker"
              :class="{blank:this.form.gender!='Gender'}"
              class="GenderDiv"
              ref="Gender"
            >{{this.form.gender}}</div>
          </cube-button>
        </div>
        <div class="form-item">
          <p>{{$t('Surname')}}</p>
          <!-- @blur="SurnameBlur" -->
          <input class="Gender" v-model="form.surname" type="text" placeholder="Surname" />
        </div>

        <div class="form-item">
          <p>{{$t('Given_Name')}}</p>
          <!-- @blur="givenameBlur" -->
          <input class="Gender" v-model="form.givename" type="text" placeholder="Given Name" />
        </div>
        <!-- 日期插件 -->
        <cube-button>
          <div class="form-item">
            <p>{{$t('Date_of_Birth')}}</p>
            <!-- @blur="birthDateBlur" -->
            <input
              ref="inputBirth"
              class="Gender"
              @focus="showDatePicker"
              v-model="form.datebirth"
              type="text"
              placeholder="Date of birth"
            />
          </div>
        </cube-button>

        <!-- 选择HKID按钮和passport Number按钮 -->
        <div>
          <button class="HKID" @click="inputHkid" :class="[select==1?'blue':'']">
            {{$t('personal_hkid')}}
            <i :class="[select==1?'cubeic-ok':'']"></i>
          </button>
          <button class="passport" @click="inputPassport" :class="[select==2?'blue':'']">
            {{$t('info_passport')}}
            <i :class="[select==2?'cubeic-ok':'']"></i>
          </button>
        </div>
        <!-- 输入框 -->
        <div class="HK">
          <span class="hkNumber">{{select==1?`${$t('account_info_hkid')}:`:`${$t('info_passport')}:`}}</span>
          <div class="right" v-show="select == 1">
            <span>
              <!-- @blur="HkidBlur" -->
              <input
                v-model="form.HKID1"
                class="IDnumber"
                ref="HKID1"
                type="text"
                placeholder="A123456"
              />
            </span>
            <span v-show="select == 1">(</span>
            <span v-show="select == 1">
              <input ref="HKID2" v-model="form.HKID2" class="num" type="text" placeholder="7" />
            </span>
            <span v-show="select == 1">)</span>
          </div>
          <div class="right" v-show="select == 2">
            <span>
              <!--  @blur="passportBlur" -->
              <input
                ref="HKID_passport"
                v-model="form.HKID_passport"
                :class="[select==2?'width':'']"
                class="IDnumber"
                type="text"
                placeholder="A123456"
              />
            </span>
          </div>
        </div>
      </div>
    </div>
    <div class="button-bar">
      <button @click="confirm" class="proceed">Confirm</button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  name: "formItem",

  data() {
    return {
      form: {
        gender: "Gender",
        surname: "",
        givename: "",
        datebirth: "",
        HKID_passport: "",
        HKID1: "",
        HKID2: "",
        title: "Policyholder"
      },
      //判断点击之后的按钮
      select: 1,
      //保存年龄
      ages:''
    };
  },
  created() {
    console.log(this.form);
    this.getProtectorInfo();
    console.log(this.form);
    //如果没选myself访问这个网站就打回首页
    if (!this.travelInfo || this.travelInfo.count.myself != 0) {
      this.$router.push(`/index/${this.product_id}/${this.language}`);
    }
  },
  methods: {
    ...mapMutations(["setProtectorInfo"]),
    inputHkid() {
      //如果有值就把select改为2
      if (this.$refs.HKID_passport.value.trim() != "") {
        this.$createToast({
          txt: this.$t('onlyOne'),
          time: 3000,
          type: "txt"
        }).show();
        this.select = 2;
      } else {
        this.select = 1;
      }
    },
    inputPassport() {
      if (
        this.$refs.HKID1.value.trim() != "" ||
        this.$refs.HKID2.value.trim() != ""
      ) {
        this.$createToast({
          txt: this.$t('onlyOne'),
          time: 3000,
          type: "txt"
        }).show();
        this.select = 1;
      } else {
        this.select = 2;
      }
    },
    //确认事件
    async confirm() {
      console.log(this.form);
      //验证信息是否都填写完毕
      if (
        this.form.datebirth == "" ||
        this.form.givename == "" ||
        this.form.gender == "" ||
        this.form.surname == ""
      ) {
        this.$createToast({
          txt: this.$t('notInformation'),
          time: 3000,
          type: "txt"
        }).show();
        return false;
      }
      if (this.form.HKID1 == "" && this.form.HKID_passport == "") {
        this.$createToast({
          txt: this.$t('notInformation'),
          time: 3000,
          type: "txt"
        }).show();
        return false;
      }
      if (this.form.HKID1 != "" && this.form.HKID2 == "") {
        this.$createToast({
          txt: this.$t('notInformation'),
          time: 3000,
          type: "txt"
        }).show();
        return false;
      }
      //判断是否大于18岁
      // if (this.ages < 18) {
      //   this.$createToast({
      //     txt: this.$t('policyholder_number'),
      //     time: 3000,
      //     type: "txt"
      //   }).show();
      //   return false;
      // }
      //调用验证方法,验证信息填写是否符合规定
      var isverify1 = await this.verifyUserFrom(this.form);
      if (isverify1 == false) {
        return false;
      }
      //如果验证都通过
      console.log(isverify1);
      if (isverify1 == true) {
        this.form.gender = this.form.gender || "";
        this.form.surname = this.form.surname || "";
        this.form.givename = this.form.givename || "";
        this.form.datebirth = this.form.datebirth || "";
        this.form.HKID1 = this.form.HKID1 || "";
        this.form.HKID2 = this.form.HKID2 || "";
        this.form.HKID_passport = this.form.HKID_passport || "";
        this.form.title = "Policyholder";
        //把数据存到vuex中
        this.setProtectorInfo(this.form);
        this.$router.push({ path: "/confirmOrder" });
      }
    },
    //如果vuex中有保单人信息,获取渲染到页面
    getProtectorInfo() {
      if (this.$store.state.protectorInfo) {
        console.log("当protectorInfo有值时,把值赋值给form");
        for (var key in this.$store.state.protectorInfo) {
          this.form[key] = this.$store.state.protectorInfo[key];
        }
        //判断再次进入信息编辑页的时候passport是否有值
        if (this.form.HKID_passport != "") {
          this.select = 2;
        }
      }
    },
    //封装判断用户输入的信息是否符合规定
    async verifyUserFrom(protectorInfo) {
      var isverify = false;
      //验证用户surname
      var surnameinput = new RegExp("[a-zA-Z]");
      if (!surnameinput.test(protectorInfo.surname)) {
        this.$createToast({
          txt: this.$t('LastNameIncorrect'),
          time: 3500,
          type: "txt"
        }).show();
        return false;
      }
      //验证用户given name
      var surnameinput1 = new RegExp("[a-zA-Z]");
      if (!surnameinput1.test(protectorInfo.givename)) {
        this.$createToast({
          txt: this.$t('NameIncorrect'),
          time: 3500,
          type: "txt"
        }).show();
        return false;
      }
      //验证passport
      var passportInput = new RegExp("^[0-9]*$");
      if (
        protectorInfo.HKID_passport.trim() != "" &&
        !passportInput.test(protectorInfo.HKID_passport)
      ) {
        this.$createToast({
          txt: this.$t('passportErr'),
          time: 3500,
          type: "txt"
        }).show();
        return false;
      }
      //验证HKID(调用接口)
     if (this.form.HKID1 != "") {
        let HKID = this.form.HKID1 + this.form.HKID2;
        //P1032651
        console.log(HKID);
        try {
          const res = await this.$http.post(
            "/wesupromo/web/validate/validateHKIDWithLng",
            {
              language: "tc",
              hkid: HKID
            }
          );
          console.log(res);
          console.log(res.status);
        } catch (err) {
          console.log(err);
          if (err.data.is_valid == false) {
            const toast = this.$createToast({
              time: 3500,
              type: "txt",
              txt: err.msg
            });
            toast.show();
            return false;
          }
        }
      }
      isverify = true;
      console.log(isverify);
      return isverify;
    },
    //点击选择性别插件
    showPicker() {
      const column1 = [
        { text: this.$t('Female'), value: "Male" },
        { text: this.$t('Male'), value: "Female" },
        
      ];
      if (!this.picker) {
        this.picker = this.$createPicker({
          title: this.$t('Gender'),
          data: [column1],
          cancelTxt: this.$t("cancel"),
          confirmTxt: this.$t("confirm"),
          onSelect: this.selectGender,
          onCancel: this.cancelGender
        });
      }
      this.picker.show();
    },
    //选择性别点击确认事件
    selectGender(selectedVal, selectedIndex, selectedText) {
      this.form.gender = selectedText.join(" ");
      console.log(this.form.gender);
      
    },
    //选择性别点击取消事件
    cancelGender() {},
    //点击日期插件
    showDatePicker() {
      if (!this.datePicker) {
        this.datePicker = this.$createDatePicker({
          title: this.$t('account_info_birthday'),
          min: new Date(1980, 7, 28),
          max: new Date(),
          value: new Date(),
          cancelTxt: this.$t("cancel"),
          confirmTxt: this.$t("confirm"),
          onSelect: this.selectHandle,
          onCancel: this.cancelHandle
        });
      }
      this.datePicker.show();
    },
    //点击确认事件
    selectHandle(date, selectedVal, selectedText) {
      this.form.datebirth = this.dateTozero(selectedVal).join("-");
      console.log(this.form.datebirth);
      //因为cube-ui再次选完日期不会修改日期的显示
      this.$refs.inputBirth.value = this.form.datebirth;
      this.getYears()
    },
    //点击取消
    cancelHandle() {},
    //数字小于10时前面加个0
    dateTozero(arr) {
      return arr.map(item => (item < 10 ? "0" + item : "" + item));
    },
    //返回上一页
    toPrePage(){
      this.$router.push('/MemberData')
    },
    // 获取当天的天数
    getToday(date) {
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      var d = date.getDate();
      d = d < 10 ? "0" + d : d;
      return y + "/" + m + "/" + d ;
    },
    //计算年龄(是否大于十八岁)
    getYears(){
      let todayDate =  this.getToday(new Date());
      let years =
          Math.ceil(
            (new Date(`${todayDate} 00:00:00`).getTime() -
              new Date(`${this.form.datebirth.replace(/-/g,'/')} 00:00:00`).getTime()) /
              (1000 * 60 * 60 * 24)
          );
      this.ages = Math.floor(years / 365);
      console.log(this.ages + '岁');
    }
  },
  computed: {
    ...mapState(["travelInfo", "product_id", "language", "plan"])
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.contain {
  padding: 0 30px;
  padding-top: 100px;
  background-color: #fff;
  height: 100%;
  position: relative;
  .hint {
    color: rgb(88, 89, 95);
    margin-bottom: 30px;
    margin-top: 50px;
  }
  .form-wrap {
    padding: 20px;
    box-shadow: 0px 5px 10px 0 rgba(0, 0, 0, 0.14);
    // line-height: 65px;
    background-color: #fff;
    .title {
      font-size: 32px;
      color: @blue;
      font-weight: bold;
      margin-top: 20px;
      margin-bottom: 50px;
    }
    p {
      display: flex;
      justify-content: space-between;
      .member {
        color: @blue;
        font-weight: bold;
        font-size: 32px;
      }
      .member-num {
        color: @blue;
        font-weight: bold;
        font-size: 32px;
        margin-left: 15px;
      }
      .member-title {
        font-weight: bold;
        font-size: 32px;
        color: rgb(0, 0, 0);
        font-family: HelveticaNeue-Medium;
      }
    }
    .cubeButton {
      width: 100%;
      height: 75px;
      .GenderDiv {
        height: 75px;
        border-radius: 15px;
        font-size: 30px;
        border: 2px solid rgba(155, 155, 155, 0.5);
        color: #9b9b9b;
        line-height: 75px;
        text-align: left;
        padding-left: 18px;
        font-weight: 300;
      }
      .blank {
        color: rgb(0, 0, 0);
        font-weight: bold;
      }
    }
    .Gender {
      height: 75px;
      width: 100%;
      border-radius: 15px;
      font-size: 30px;
      border: 2px solid rgba(155, 155, 155, 0.5);
      text-indent: 20px;
      font-weight: bold;
    }
    input::-webkit-input-placeholder {
      color: #9b9b9b;
      letter-spacing: 2px;
      font-weight: 300;
    }
    .form-item {
      p {
        color: rgb(0, 0, 0);
      }
      line-height: 35px;
      margin-top: 20px;
    }
    .HKID {
      width: 316px;
      height: 56px;
      color: white;
      font-size: 30px;
      outline: none;
      border: none;
      background: #9b9b9b;
      border-radius: 10px;
      margin-top: 40px;
    }
    .passport {
      width: 316px;
      height: 56px;
      padding: 0;
      text-align: center;
      color: white;
      font-size: 30px;
      background: #9b9b9b;
      outline: none;
      border: none;
      border-radius: 10px;
      margin-top: 40px;
    }
    .blue {
      background: @blue;
    }
    .HK {
      margin-top: 30px;
      font-size: 32px;
      margin-bottom: 10px;
      display: flex;
      justify-content: space-between;
      height: 70px;
      .hkNumber {
        display: inline-block;
        height: 70px;
        line-height: 70px;
      }
      .right {
        display: inline-block;
      }
      .hkNumber {
        color: rgb(0, 0, 0);
        // font-weight: bold;
        font-family: HelveticaNeue;
      }
      span {
        .IDnumber {
          width: 235px;
          height: 60px;
          border-radius: 13px;
          font-size: 30px;
          padding-right: 15px;
          border: 2px solid rgba(155, 155, 155, 0.5);
          margin-left: 20px;
          text-align: right;
        }
        .num {
          width: 75px;
          height: 60px;
          border-radius: 13px;
          font-size: 30px;
          border: 2px solid rgba(155, 155, 155, 0.5);
          text-align: center;
        }
        .width {
          width: 300px;
        }
      }
    }
  }
  .button-bar {
    margin: 0 auto;
    text-align: center;
    position: fixed;
    width: 100%;
    height: 240px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    border-top: 3px solid rgba(0, 0, 0, 0.25);
    background-color: rgba(250, 250, 250);
  }
  .proceed {
    width: 388px;
    height: 130px;
    line-height: 130px;
    border: none;
    margin-top: 50px;
    border-radius: 10px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
  .cube-btn {
    padding: 0;
    background-color: #fff;
  }
   .headTitle {
    position: fixed;
    height: 100px;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 99;
    background-color: rgba(250, 250, 250);
    border-bottom: 2px solid rgba(0, 0, 0, 0.25);
    .cubeic-arrow {
      color: rgb(0, 122, 255);
      display: inline-block;
      transform: rotateZ(180deg);
      font-size: 32px;
      font-weight: bold;
      position: absolute;
      left: 0px;
      line-height: 100px;
      width: 50px;
      text-align: center;
      height: 100%;
    }
    p{
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      display: inline-block;
      font-size: 30px;
      font-weight: 600;
      margin-top: 40px;
    }
  }
}
</style>


